.shape-layer {
  position: absolute;
  user-select: none; }

.shape-layer .shape-content {
  width: 100%;
  height: 100%;
  overflow: hidden; }

.shape-layer.active {
  outline: 1px solid #70c0ff !important;
  z-index: 1; }

.shape-scale__point {
  position: absolute;
  background: #fff;
  border: 1px solid #59c7f9;
  width: 8px;
  height: 8px;
  z-index: 1;
  border-radius: 50%;
  user-select: none;
  box-sizing: border-box;
  margin-top: -4px;
  margin-left: -4px; }

.shape-scale__point[data-point="lt"] {
  cursor: nw-resize; }

.shape-scale__point[data-point="rt"] {
  cursor: ne-resize; }

.shape-scale__point[data-point="lb"] {
  cursor: sw-resize; }

.shape-scale__point[data-point="rb"] {
  cursor: se-resize; }

.shape-scale__point[data-point="lm"] {
  cursor: w-resize; }

.shape-scale__point[data-point="tm"] {
  cursor: n-resize; }

.shape-scale__point[data-point="rm"] {
  cursor: e-resize; }

.shape-scale__point[data-point="bm"] {
  cursor: s-resize; }
