@keyframes rotating {
  0% {
    transform: rotate(0deg); }
  to {
    transform: rotate(1turn); } }

.bg-music-wrapper {
  right: 0;
  top: 0;
  position: absolute;
  z-index: 200;
  width: 30px;
  height: 30px; }
  .bg-music-wrapper .bg-music-btn {
    position: absolute;
    z-index: 200;
    width: 30px;
    height: 30px;
    right: 0;
    top: 0;
    border-radius: 15px;
    background-image: url(./bg-music.svg);
    background-size: contain;
    background-repeat: no-repeat; }
    .bg-music-wrapper .bg-music-btn.rotate {
      animation: rotating 1.2s linear infinite; }
