.shortcut-button-wrapper {
  cursor: pointer; }

.shortcut-button {
  display: flex !important;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 60px !important;
  width: 100%;
  margin-bottom: 5px;
  border: 1px dashed #fff !important;
  background-color: #f5f8fb !important;
  color: #393e46 !important;
  user-select: none; }
  .shortcut-button:disabled {
    cursor: not-allowed; }
  .shortcut-button .shortcut-icon {
    padding: 4px; }
  .shortcut-button span {
    pointer-events: none; }

.page-manager-panel {
  position: relative;
  max-height: calc(100vh - 200px);
  overflow: auto; }
  .page-manager-panel__item {
    display: flex;
    justify-content: space-between;
    padding: 12px 0;
    height: 60px;
    border-bottom: 1px solid #f0f4f5; }
    .page-manager-panel__item.active {
      color: #1593ff; }
  .page-manager-panel .footer-actions {
    position: fixed;
    bottom: 10px;
    margin-left: 40px; }

.contextmenu__horizontal-menus {
  height: 32px;
  line-height: 32px;
  border: none;
  border-top: 1px solid #eee; }
  .contextmenu__horizontal-menus__item {
    padding: 0 4px 4px; }

.contextmenu__vertical-menus {
  border: none; }
  .contextmenu__vertical-menus__item {
    height: 22px !important;
    line-height: 22px !important;
    margin: 0; }

/**
 * #!zh: 元素对齐参考线
 * #!en: align objects with guides
 */
.luban-auxiliary-line {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

.v-line {
  position: absolute;
  height: 100%;
  width: 1px;
  top: 0;
  background-color: #94f5ff; }

.h-line {
  position: absolute;
  height: 1px;
  width: 100%;
  left: 0;
  background-color: #94f5ff; }

.hidden {
  display: none !important; }

.dragging-dom-ele {
  position: fixed !important;
  z-index: 100;
  display: inline-block;
  box-sizing: border-box;
  height: 30px;
  width: 130px;
  border-radius: 4px;
  border: 1px solid #ccc;
  cursor: grabbing;
  user-select: none;
  opacity: 0.6; }

.ant-tabs-nav .ant-tabs-tab {
  padding: 12px 0 !important; }

.avaiable-animations-tabs .ant-tabs-nav .ant-tabs-tab {
  padding: 12px 16px !important; }

#animation-right-panel .ant-collapse-header {
  padding: 6px 0 6px 40px; }

#animation-right-panel .collapse-wrapper {
  margin-top: 12px; }
  #animation-right-panel .collapse-wrapper .ant-form-item {
    margin-bottom: 0; }

.default-router-link {
  color: rgba(0, 0, 0, 0.65); }

.router-link-active {
  color: #1890ff !important;
  background-color: transparent;
  text-decoration: none; }

.plugin-usage-tip {
  font-size: 12px;
  color: #999;
  border-left: 0.2rem solid #dfe2e5;
  padding: 0.25rem 0 0.25rem 0.5rem; }

.ant-layout-sider {
  transition: none !important; }

.adjust-line-wrapper .adjust-line {
  border: 1px solid #eee; }

.adjust-line-wrapper .adjust-line-h {
  width: 100%; }

.adjust-line-wrapper .adjust-line-v {
  height: 100%; }

.adjust-line-wrapper .adjust-button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  width: 30px;
  height: 10px;
  background-color: #fff; }
  .adjust-line-wrapper .adjust-button .indicator {
    margin: 0 auto;
    width: 10px;
    height: 1px;
    background-color: #ccd5db; }

.adjust-line-wrapper:hover .adjust-button {
  background-color: #1593ff; }

.adjust-line-wrapper:hover .adjust-line {
  border-color: #1593ff; }

.adjust-line-wrapper-h {
  cursor: ns-resize; }
  .adjust-line-wrapper-h .adjust-tip {
    letter-spacing: 1px;
    padding: 0;
    height: 24px;
    margin: 2px auto 0;
    text-align: center;
    line-height: 24px;
    font-size: 12px;
    color: #868484;
    vertical-align: middle; }

.adjust-line-wrapper-v {
  position: relative;
  height: 100%;
  cursor: ew-resize; }
  .adjust-line-wrapper-v .adjust-button {
    position: absolute;
    right: -10px;
    top: 50%;
    transform: rotate(90deg); }

/**
 * 编辑器中间的画布
 */
#canvas-outer-wrapper {
  padding: 60px 16px 24px; }

#canvas-outer-wrapper .scroll-view {
  overflow: auto; }

#canvas-outer-wrapper .scroll-view .content {
  position: relative; }

.mode-toggle-wrapper {
  position: fixed;
  z-index: 999;
  top: 24px;
  left: calc(50% - 147px);
  text-align: center; }
