.lbp-rc-wrapper {
  position: relative;
  height: 20px;
  clear: both; }
  .lbp-rc-wrapper .lbp-rc-input {
    display: none; }
  .lbp-rc-wrapper .tag {
    float: left;
    margin-left: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }
  .lbp-rc-wrapper .lbp-rc-input + label {
    position: absolute;
    left: 0;
    top: 2px;
    appearance: none;
    background-color: #fafafa;
    border: 1px solid #cacece; }

.lbp-checkbox + label {
  top: 2px;
  padding: 6px;
  border-radius: 2px; }

.lbp-checkbox:checked + label:after {
  content: " ";
  position: absolute;
  top: 0.5px;
  left: 3.5px;
  z-index: 999;
  width: 4.5px;
  height: 7.5px;
  border-style: solid;
  border-width: 0 1px 1px 0;
  transform: rotate(45deg); }

.lbp-radio + label {
  padding: 6px;
  border-radius: 50px;
  display: inline-block; }

.lbp-radio:checked + label:after {
  content: ' ';
  position: absolute;
  top: 3px;
  left: 3px;
  width: 6px;
  height: 6px;
  border-radius: 50px;
  background: #99a1a7;
  box-shadow: inset 0px 0px 10px rgba(0, 0, 0, 0.3);
  text-shadow: 0px;
  font-size: 32px; }
