.lbp-table {
  position: relative;
  overflow: hidden; }
  .lbp-table table {
    position: absolute;
    table-layout: fixed;
    height: 100%;
    background: white; }
    .lbp-table table td {
      border-top: 2px solid #ebeef5;
      border-left: 2px solid #ebeef5; }
    .lbp-table table tr:last-child {
      border-bottom: 2px solid #ebeef5;
      border-right: 2px solid #ebeef5; }
    .lbp-table table tr:first-child {
      background-color: #f6f6f6;
      font-size: 13px;
      font-weight: bold; }
    .lbp-table table td {
      text-align: center; }
  .lbp-table .main-table-wrapper {
    position: absolute;
    left: 0px;
    top: 0px;
    overflow: auto;
    width: 100%;
    height: 100%; }
  .lbp-table .fixed-table-wrapper {
    position: absolute;
    left: 0px;
    top: 0px;
    overflow: hidden; }

.lbp-table-theme-stripe table tbody tr:nth-child(odd):not(:first-child) {
  background-color: #f6f6f6; }

.lbp-table-theme-stripe table tbody tr:nth-child(even) {
  background-color: white; }

.lbp-table-theme-light-blue table tbody tr:first-child {
  background-color: #edf8ff; }

.lbp-table-theme-light-blue table tbody tr:nth-child(odd):not(:first-child) {
  background-color: #edf8ff; }

.lbp-table-theme-light-blue table tbody tr:nth-child(even) {
  background-color: white; }
